






























































import { Component, Vue } from "vue-property-decorator";
import { GetRecordModel } from "@/model/RecordModel.ts";
import TagModule from "@/model/TagModel.ts";
import Icon from "@/components/Icon.vue";
import Time from "@/lib/Time";

@Component({
  components: { Icon },
})
export default class ShowRecord extends Vue {
  IconName = [
    "编辑",
    "餐饮",
    "工资",
    "购物",
    "红包",
    "奖金",
    "交通",
    "教育",
    "其他",
    "医疗",
    "娱乐",
  ];
  Day = Time();
  RecordModel: any = {
    RecordList: [],
    CurrentMonthResidue() {},
    CurrentMonthSpending() {},
    CurrentMonthIncome() {},
    getRecord() {},
    saveRecord() {},
    addRecord() {},
  };
  mounted() {
    GetRecordModel().then((result: any) => {
      this.RecordModel = result;
    });
  }
  get recordList() {
    const hash: { [key: string]: RecordItem[] } = {};
    this.RecordModel.RecordList.forEach((record: RecordItem) => {
      const key = record.date;
      if (!(key in hash)) {
        hash[key] = [];
      }
      hash[key].push(record);
    });
    const array = Object.entries(hash).sort((a, b) => {
      if (a[0] === b[0]) return 0;
      if (a[0] > b[0]) return -1;
      if (a[0] < b[0]) return 1;
      return 0;
    });
    return array;
  }
  get tags() {
    return TagModule.TagList;
  }
}
